import formTarifJasaJobType from "./formTarifJasaJobType";
import { POST, DELETE } from '../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox"

export default {
  name: "client",
  components: { formTarifJasaJobType, HalfCircleSpinner },
  data() {
    return {
      isLoading: false,
      isShow: false,
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      disableView: false,
      disableEdit: false,
      textTitle: "",
      dataTarifJasJobType: [],
      form: {
        Id: null,
        TarifJasaCode: null,
        JobTypeCode: null,
        ProductCode: null,
        Price: null,
        OvertimePrice: null
      },
      headers: [{
        text: "Aksi",
        value: "aksi",
        sortable: false,
        align: "left",
        class: ""
      }, {
        text: "Tarif Jasa Code",
        value: "TarifJasaCode",
        align: "center"
      }, {
        text: "Job Type Code",
        value: "JobTypeCode",
        align: "center"
      }, {
        text: "Product Code",
        value: "ProductCode",
        align: "center"
      }, {
        text: "Price",
        value: "Price",
        align: "center"
      }, {
        text: "Overtime Price",
        value: "OvertimePrice",
        align: "center"
      }]
    };
  },
  created() {
    this.loadData()
  },
  beforeMount: function () { },
  computed: {},
  methods: {
    toggleInputDialog: function () {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Tarif Jasa Job Type"
    },
    onClose: function () {
      this.isShow = false;
      this.disableEdit = false;
      this.disableView = false;
      this.resetForm()
    },
    loadData: function () {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`tarifjasa/tarifjasa_cr/${page}/${rowsPerPage}`).then((res) => {
          this.dataTarifJasJobType = res.Data.map(x => {
            return ({
              Id: x.Id,
              TarifJasaCode: x.TarifJasaCode,
              JobTypeCode: x.JobTypeCode,
              ProductCode: x.ProductCode,
              Price: x.Price,
              OvertimePrice: x.OvertimePrice
            })
          })
          this.totalData = res.TotalData;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onEdit: function (id) {
      try {
        this.isLoading = true
        POST('tarifjasa/tarifjasa_cr', { Id: id }).then((res) => {
          this.disableView = false;
          this.disableEdit = true;
          this.form = res[0]
          this.textTitle = "Ubah Tarif Jasa Job Type"
          this.isShow = true;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    onDelete: function (id) {
      deleteData(id, this.delData)
    },
    delData: function (id) {
      try {
        this.isLoading = true
        DELETE(`tarifjasa/${id}`).then((res) => {
          success("Data Berhasil Dihapus")
          this.loadData()
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onView: function (id) {
      try {
        this.isLoading = true;
        POST('tarifjasa/tarifjasa_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = true;
          this.isShow = true;
          this.textTitle = "Lihat Tarif Jasa Job Type"
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    resetForm: function () {
      this.form = {
        Id: null,
        TarifJasaCode: null,
        JobTypeCode: null,
        ProductCode: null,
        Price: null,
        OvertimePrice: null
      }
    }
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`tarifjasa/tarifjasa_cr/${page}/${rowsPerPage}`).then((res) => {
            this.dataTarifJasJobType = res.Data.map(x => {
              return ({
                Id: x.Id,
                TarifJasaCode: x.TarifJasaCode,
                JobTypeCode: x.JobTypeCode,
                ProductCode: x.ProductCode,
                Price: x.Price,
                OvertimePrice: x.OvertimePrice
              })
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  }
};
